import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    max-width : 500px; 
    margin:0 auto;
    
`
const List = styled.ul`
    display:flex;
    justify-content: center;

`

const Paginate = props => {
    return (

        <Wrapper>
        <List>{props.children}</List>
        </Wrapper>
    )

}

export default Paginate;



