import React from 'react'
import styled from 'styled-components'
import Link from 'gatsby-link'

const Center = styled.li`
  display: table-cell;
  width:30px;
  height: 30px;
  margin:5px;
  text-align:center;
  vertical-align: middle;
  background: ${props => (props.page === props.index ?
                       props.theme.colors.highlight :
                       props.theme.colors.base)};
  &:hover {
    background: ${props => props.theme.colors.highlight};
  }
  transition: 0.2s;
  border-radius: 2px;
  padding-top:5px;
  color: white;
  a {
    display:block;
    width:100%;
    height:100%;
    color: white;
    text-decoration: none;
  }
  @media screen and (max-width: ${props => props.theme.responsive.small}) {
      display: ${props => (props.page === props.index ?
                          'inline' :
                          'none')};
  }
`

const PaginateCenter = props => {
    const currentPage = props.page == 1 ? `` : `/${props.page}`
    if (props.page === props.index) {
        return (
            <Center page={props.page} index={props.index}>
                {props.page}
            </Center>
        )
    } else {
        return (
            <Center page={props.page} index={props.index}>
                <Link to={currentPage}>
                    {props.page}
                </Link>
            </Center>
        )
    }
}

export default PaginateCenter 


