import React from 'react'
import CardList from '../components/CardList'
import Card from '../components/Card'
import Paginate from '../components/Paginate'
import PaginateLeft from '../components/PaginateLeft'
import PaginateCenter from '../components/PaginateCenter'
import PaginateRight from '../components/PaginateRight'
import Container from '../components/Container'
import PageTitle from '../components/PageTitle'
import SEO from '../components/SEO'

const Index = ({ pathContext }) => {
  const { group, index, pageCount } = pathContext
  let pageCountArray, count, start
  if (pageCount < 10) {
    count = pageCount
    start = 1
  } else {
    count = 10
    if (index > 5) {
      if (pageCount - index > 4) {
        start = index - 5
      } else {
        start = pageCount - 9
      }
    } else {
      start = 1
    }
  }
  pageCountArray = Array.from(new Array((count))).map((v, i) => i + start)

  return (
    <div>
      <SEO />
      <Container>
        <PageTitle small>
          <span>👽</span>
          <span>YamaLeaks</span>
          <span>👽</span>
        </PageTitle>
        <CardList>
          {group.map(({ node: post }) => (
            <Card
              key={post.id}
              slug={post.slug}
              image={post.heroImage}
              title={post.title}
              date={post.publishDate}
              excerpt={post.body}
            />
          ))}
        </CardList>
        <Paginate>
          <PaginateLeft 
             key='prev' 
             index={index}
             />
          {pageCountArray.map((v) => (
            <PaginateCenter
              key={v}
              page={v}
              index={index}
            />
          ))}
          <PaginateRight 
             key='next' 
             index={index} 
             pageCount={pageCount} 
             />
        </Paginate>



      </Container>
    </div>
  )
}


export default Index



