import React from 'react'
import styled from 'styled-components'
import Link from 'gatsby-link'

const Prev = styled.li`
  display: table-cell;
  width:30px;
  height: 30px;
  margin:5px;
  text-align:center;
  vertical-align: middle;
  background: ${props => props.theme.colors.base};
  &:hover {
    background: ${props => props.theme.colors.highlight};
  }
  transition: 0.2s;
  border-radius: 2px;
  padding-top:5px;
  color: white;
  a {
    display:block;
    width:100%;
    height:100%;
    color: white;
    text-decoration: none;
  }
`
const lt = `<<`

const PaginateLeft = props => {
    const prevPage = props.index == 2 ? `` : `/${props.index-1}`
    if (props.index === 1) {
        return (
            null
        )
    } else {
        return (
            <Prev>
            <Link to={prevPage}>
                {lt}
            </Link>
            </Prev>
        )
    }
}

export default PaginateLeft 


