import React from 'react' 
import styled from 'styled-components' 
import Link from 'gatsby-link'

const Next = styled.li`
  display: table-cell;
  width:30px;
  height: 30px;
  margin:5px;
  text-align:center;
  vertical-align: middle;
  background: ${props => props.theme.colors.base};
  &:hover {
    background: ${props => props.theme.colors.highlight};
  }
  transition: 0.2s;
  border-radius: 2px;
  padding-top:5px;
  color: white;
  a {
    display:block;
    width:100%;
    height:100%;
    color: white;
    text-decoration: none;
  }
` 
const gt = `>>` 

const PaginateRight = props => {
    const nextPage = props.index == props.pageCount ? `` : `/${props.index+1}`
    if (props.index === props.pageCount) {
        return (
            null
        )
    } else {
        return (
            <Next>
            <Link to={nextPage}>
                {gt}
            </Link>
            </Next>
        )
    }
}

export default PaginateRight 


